.App {
  text-align: center;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.register-form {
  display: flexbox;
  margin: 10%;
  background-color: lightgreen;
  padding: 5%;
  border-radius: 45px;
}
.register-field {
  margin: 10px;
}

.register-btn {
  margin-top: 20px;
  padding: 10px 20px;
  border-radius: 15px;
  background-color: white;
}

.register-btn:hover {
  background-color: lightblue;
}

.pink-color {
  background-color: pink;
  color: black;
  border-radius: 15px;
  padding: 10px 20px;
}

.blue-color {
  background-color: blue;
  color: white;
  border-radius: 15px;
  padding: 10px 20px;
}
